<template>
    <div>
        <div class="hall">
            <div class="header">
                <div class="logo">
                    LOGO
                </div>
                <div class="header-btn" v-show="status">
                    <mt-button class="regist-btn" @click="toRegist">注册</mt-button>
                    <mt-button class="login-btn" @click="toLogin">登录</mt-button>
                </div>
                <div class="header-usr" v-show="!status"> 
                    {{this.$store.state.songInfo.user.nickname}},欢迎您
                </div>
            </div>
            <div class="banner">
                <img src="./../assets/游戏banner1.jpg">
            </div>
            <div class="radio pageContent">
                <img src="./../assets/喇叭@2x.png">
                <div class="radio-text">
                    <transition-group name="radio-list">
                        <span class="radio-list" v-for="item in radioText" :key="item">{{item}}</span>
                    </transition-group>
                </div>
            </div>
            <div class="pageContent mt10">
                <div class="module">
                    <div class="title">棋牌游戏</div>
                    <div class="game-list">
                        <div class="game-box">
                            <div class="game-img">
                                <img src="./../assets/形状3@2x.png">
                            </div>
                            <div class="game">
                                <div class="game-name">百家乐</div>
                                <div class="game-info">超过百万用户</div>
                            </div>
                        </div>
                        <div class="game-box">
                            <div class="game-img">
                                <img src="./../assets/椭圆4拷贝3@2x.png">
                            </div>
                            <div class="game">
                                <div class="game-name">三公</div>
                                <div class="game-info">超过百万用户</div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="module mt20">
                    <div class="title">热门彩票</div>
                    <div class="game-list game-caipiao">
                        <div class="game-box">
                            <div class="game">
                                <div class="game-name">重庆时时彩</div>
                                <div class="game-info">超过百万用户</div>
                            </div>
                            <div class="game-img">
                                <img src="./../assets/21@2x.png">
                            </div>
                        </div>
                        <div class="game-box">
                            <div class="game">
                                <div class="game-name">香港六合彩</div>
                                <div class="game-info">超过百万用户</div>
                            </div>
                            <div class="game-img">
                                <img src="./../assets/香港六合彩21@2x.png">
                            </div>
                        </div>
                    </div>
                    <div class="game-list game-caipiao">
                        <div class="game-box">
                            <div class="game">
                                <div class="game-name">北京PK10</div>
                                <div class="game-info">超过百万用户</div>
                            </div>
                            <div class="game-img">
                                <img src="./../assets/10@2x.png">
                            </div>
                        </div>
                        <div class="game-box">
                            <div class="game">
                                <div class="game-name">广东11选5</div>
                                <div class="game-info">超过百万用户</div>
                            </div>
                            <div class="game-img">
                                <img src="./../assets/形状4拷贝@2x.png">
                            </div>
                        </div>
                    </div>
                    <div class="game-list game-caipiao">
                        <div class="game-box">
                            <div class="game">
                                <div class="game-name">江苏快3</div>
                                <div class="game-info">超过百万用户</div>
                            </div>
                            <div class="game-img">
                                <img src="./../assets/组5@2x.png">
                            </div>
                        </div>
                        <div class="game-box">
                            <div class="game">
                                <div class="game-name">分分彩</div>
                                <div class="game-info">超过百万用户</div>
                            </div>
                            <div class="game-img">
                                <img src="./../assets/彩@2x.png">
                            </div>
                        </div>
                    </div>
                </div>
                <div class="module mt20">
                    <div class="title">体育竞技</div>
                    <div class="game-list">
                        <div class="game-box">
                            <div class="game-img">
                                <img src="./../assets/组7@2x.png">
                            </div>
                            <div class="game">
                                <div class="game-name">足球</div>
                                <div class="game-info">超过百万用户</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                radioText: [
                    '尊敬的会员您好，入款通道更新中...',    
                    '阿斯加德卡死了多久爱死了肯德基阿拉山口'
                ],
                status: true,

            }
        },
        created: function(){
            // console.log(this.$store.state.songInfo.code)
            if(this.$store.state.songInfo.code){
                this.status = false
            }else{
                this.status = true
            }
        },
        methods: {
            toLogin: function(){
                this.$router.push('/login')
            },
            toRegist: function(){
                this.$router.push('/regist')
            }
        },
        beforeCreate: function() {
            document.getElementsByTagName("body")[0].className="bgc-fff";
            this.$store.state.tabbarShow =false;
            
        },
        beforeDestroy: function() {
            document.body.removeAttribute("class","bgc-fff");
        },
        
    }
</script>

<style scoped>
.header{
    display: flex;
    justify-content: space-between;
    height: 45px;
    line-height: 45px;
}
.header-btn{
    padding-right: 10px;
}
.header-usr{
    padding-right: 10px;
    font-size: 14px;
    color: #ffba00;
}
.regist-btn,.login-btn{
    height: 26px;
    border-radius: 3px;
    font-size: 12px;
    color: #fff;
    width: 55px;
    margin: 0 3px;
}
.regist-btn{
    background: -webkit-linear-gradient(#DEBF26, #ff8800); /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(#DEBF26, #ff8800); /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(#DEBF26, #ff8800); /* Firefox 3.6 - 15 */
    background: linear-gradient(#DEBF26, #ff8800); /* 标准的语法 */
}
.login-btn{
    background-color: #0e56e8;
    background: -webkit-linear-gradient(#7A96EA, #0e56e8); /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(#7A96EA, #0e56e8); /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(#7A96EA, #0e56e8); /* Firefox 3.6 - 15 */
    background: linear-gradient(#7A96EA, #0e56e8); /* 标准的语法 */
}
.banner{
    height: 115px;
}
.banner img{
    width: 100%;
    height: 100%;
}
.radio{
    height: 40px;
    line-height: 40px;
    text-align: left;
    padding: 0 20px;
    display: flex;
    flex-wrap: nowrap;
}
.radio img{
    margin: 12px 15px 0 0;
    height: 14px;
    width: 18px;
}
.radio-text{
    overflow: hidden;
    height: 40px;
    line-height: 40px;
}

.radio-text span{
    height: 100%;
    position: relative;
    font-size: 12px;
    margin-right: 30px
}
.title{
    font-size: 14px;
    font-weight: 600;
    text-align: left;
}
.game-list{
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
}
.game-img{
    width: 50px;
    height: 50px;
}
.game-img img{
    width: 100%;
    height: 100%;
}
.game-box{
    width: 48%;
    display: flex;
    flex-wrap: nowrap;
    font-size: 14px;
    height: 50px;
    line-height: 50px;
}
.game{
    text-align: left;
    line-height: 25px;
    padding-left: 10px;
}
.game-caipiao .game{
    padding: 0 10px 0 0;
}
.game-name{
    color: #000;
}
.game-info{
    font-size: 12px;
    color: #999;
}

</style>


