<template>
    <div>
        <div class="pageContent loginPage" id="loginPage">
            <div class="title">
                用户登录
            </div>
            <mt-field placeholder="请输入用户名" type="tel" v-model="username" align="left"></mt-field>
            <mt-field placeholder="请输入密码" type="password" v-model="password"></mt-field>
            <div class="text">
                <div class="regist text-link" @click="toRegist">我要注册</div>
                <div class="forget text-link">忘记密码？</div>
            </div>
            <mt-button size="large" class="login-btn" @click="login">登录</mt-button>
        </div>
    </div>
</template>

<script>
import root from '@/config/root.js'
    export default {
        data(){
            return{
                username: '',
                password: '',
                status: ''
            }
        },
        methods: {
            
            login: function(){
                this.$axios.post(root.login,{
                    username: this.username,
                    password: this.password
                }).then(response => {
                    console.log(response.data);
                    if(response.data.code == 1){
                        this.$router.push('/');
                        // console.log(response);
                        this.$store.commit("getSongInfo", response.data);
                        console.log(this.$store.state.songInfo);
                    }else{
                        this.$messagebox.alert("账号密码错误，请重新登录!","提示");
                    }
                })
            },
            toRegist: function(){
                this.$router.push('/regist')
            }
        },
        mounted: function(){
            let h = window.screen.height;
            console.log(h);
            document.getElementById('loginPage').style.height = (h - 30) + 'px';
        },
        created: function(){
            // console.log(this.$store.state.isPageLogin)
            // if(this.$router.history.current.name == 'Login'){
                this.$store.state.isPageLogin = true
            // }
            // console.log(this.$store.state.isPageLogin)
        },
        destroyed: function(){
            this.$store.state.isPageLogin = false;
        },
        beforeCreate: function() {
            document.getElementsByTagName("body")[0].className="mg0";
            document.getElementsByTagName("html")[0].className="mg0";
        },
        beforeDestroy: function() {
            document.getElementsByTagName("html")[0].removeAttribute("class","mg0");
            document.body.removeAttribute("class","mg0");
        },
    }
</script>

<style scoped>
.loginPage{
    background: url('./../../assets/图层3@2x.png') no-repeat;
    background-size: 100% 100%; 
    padding: 0 20px;
}
.title{
    margin: 60px 0;
    color: #fff;
    font-size: 20px;
}
.loginPage{
    padding-top: 30px;
}
input{
    text-align: left
}
.text{
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    color: #fff;
    padding: 10px 0;
}
.login-btn{
    font-size: 16px;
    background-color: #57d6dd;
    color: #fff;
    margin-top: 10px;
}
</style>


