<template>
    <div>
        <mt-header title="添加好友">
            <router-link to="/friend" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
        </mt-header>
        <div class="pageContent">
            <div class="findBox">
                <div class="findId">
                    <input class="radio" name="type" type="radio" value="id" v-model="searchType" >
                    <mt-field label="用户ID查找：" placeholder="请输入用户ID" type="number" v-model="userId" :disabled="inputOn"></mt-field>
                </div>
                <div class="findName">
                    <input class="radio" name='type' type="radio" value="nickname" v-model="searchType">
                    <mt-field label="用户昵称查找:" placeholder="请输入用户昵称" v-model="username" :disabled="!inputOn"></mt-field>
                </div>
                <div class="btn">
                    <mt-button class="cancel">取消</mt-button>
                    <mt-button class="findbtn" @click="searchFriend">查找</mt-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import root from '@/config/root.js'
    export default {
        data(){
            return{
                userId: '',
                username: '',
                searchType: '',
                inputOn: false
            }
        },
        methods: {
            searchFriend: function(){
                if(this.userId || this.username){
                    this.$axios.post(root.addFriend,{
                        id: this.userId,
                        nickname: this.username
                    }).then(response => {
                        console.log(response)
                        // if(response.)
                        if(response.data.code==0){
                            this.$messagebox.alert('用户不存在，请重新输入查找！');
                            this.userId ='';
                            this.username= '';
                        }
                        else if(response){
                            this.$store.commit('getAddFriend', response.data);
                            this.$router.push('/searchFriend')
                        }
                    })
                }else{
                    this.$messagebox.alert('请选择输入用户ID或者昵称查找好友。')
                }
            }
        },
        watch: {
            searchType: function(){
                if(this.searchType=='id'){
                    this.inputOn = false
                }else{
                    this.inputOn = true
                }
            }
        },

        beforeCreate: function() {
            document.getElementsByTagName("body")[0].className="bgc-fff";
        },
        beforeDestroy: function() {
            document.body.removeAttribute("class","bgc-fff");
        },
    }
</script>

<style scoped>
.findId,.findName{
    display: flex;
    flex-wrap: nowrap;
}
.findBox{
    font-size: 14px;
    padding: 10px;
    position: relative;
}

.findId,.findName{
    margin: 15px 0;
}
.btn{
    display: flex;
    justify-content: space-between;
}
.cancel{
    width: 45%;
    background-color: #fff;
    border: 1px solid #57D6DD;
    border-radius: 0;
    color: #57D6DD;
    font-size: 14px;
}
.findbtn{
    width: 45%;
    background-color: #57D6DD;
    border: 1px solid #57D6DD;
    color: #fff;
    border-radius: 0;
    font-size: 14px;
}
.radio{
    margin-top: 18px;
}
</style>


