import Vue from 'vue'
import Router from 'vue-router'
import Hall from '@/components/hall'
import User from '@/components/user/index'
import Friend from '@/components/user/pageFriend/friend'
import AddFriend from '@/components/user/pageFriend/addFriend'
import Chat from '@/components/common/chat'
import SearchFriend from '@/components/user/pageFriend/searchFriend'
import Bank from '@/components/user/bank/bank'
import MySystem from '@/components/user/mySystem/mySystem'
import Personal from '@/components/user/mySystem/personal'
import TransPsd from '@/components/user/mySystem/transPsd'
import Medal from '@/components/user/medal/medal'
import Redbag from '@/components/user/redbag/redbag'
import Radio from '@/components/user/radio/radio'
import Login from '@/components/login/login'
import PutForward from '@/components/user/cash/putForward'
import BankCard from '@/components/user/cash/bankCard'
import bindBankCard from '@/components/user/cash/bindBankCard'
import Alipay from '@/components/user/cash/alipay'
import bindAlipay from '@/components/user/cash/bindAlipay'
import Wechat from '@/components/user/cash/wechat'
import bindWechat from '@/components/user/cash/bindWechat'
import Regist from '@/components/login/regist'
import Chatroom from '@/components/common/chatroom'




Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',name: 'Hall',meta:{index:0},component: Hall
    },
    {
      path: '/chatroom',name: 'Chatroom',meta:{index:1},component: Chatroom
    }
    ,
    {
      path: '/login',name: 'Login',meta:{index:1},component: Login
    },
    {
      path: '/regist',name: 'Regist',meta:{index:2},component: Regist
    },
    {
      path: '/user',name: 'User',meta:{index:2},component: User
    },{
      path: '/friend',name: 'Friend',meta:{index:3},component: Friend
    },{
      path: '/addFriend',name: 'AddFriend',meta:{index:4},component: AddFriend
    },{
      path: '/chat',name: 'Chat',meta:{index:4},component: Chat
    },{
      path: '/searchFriend',name: 'SearchFriend',meta:{index:5},component: SearchFriend
    },{
      path: '/bank',name: 'Bank',meta:{index:3},component: Bank
    },{
      path: '/mySystem',name: 'MySystem',meta:{index:3},component: MySystem
    },{
      path: '/personal',name: 'Personal',meta:{index:4},component: Personal
    },{
      path: '/transPsd',name: 'TransPsd',meta:{index:4},component: TransPsd
    },{
      path: '/medal',name: 'Medal',meta:{index:3},component: Medal
    },{
      path: '/redbag',name: 'Redbag',meta:{index:3},component: Redbag
    },{
      path: '/radio',name: 'Radio',meta:{index:3},component: Radio
    },{
      path: '/putForward',name: 'PutForward',meta:{index:3},component: PutForward
    },{
      path: '/bankCard',name: 'BankCard',meta:{index:4},component: BankCard
    },{
      path: '/bindBankCard',name: 'bindBankCard',meta:{index:4},component: bindBankCard
    },{
      path: '/alipay',name: 'Alipay',meta:{index:4},component: Alipay
    },{
      path: '/bindAlipay',name: 'bindAlipay',meta:{index:4},component: bindAlipay
    },{
      path: '/wechat',name: 'Wechat',meta:{index:4},component: Wechat
    },{
      path: '/bindWechat',name: 'bindWechat',meta:{index:4},component: bindWechat
    }
  ]
})



