<template>
    <div>
        <!-- <mt-header :title="this.$route.query.val.nickname"> -->
        <mt-header title="姓名">
            <router-link to="/friend" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
            <router-link to="/addFriend" slot="right">
                <mt-button><i class="iconfont icon-yonghu" style="font-size:30px"></i></mt-button>
            </router-link>
        </mt-header>
        <div class="pageContent chat" id="chat">
            <div>
                <div class="list">
                    <div class="head">
                        <img src="./../../assets/pic1.png">
                    </div>
                    <div class="msgText">
                        <p>加拉速度进来撒肯德基萨拉斯加拉速度进来撒肯德基萨拉斯加拉速度进来撒肯德基萨拉斯加拉速度进来撒肯德基萨拉斯</p>
                        <div class="boo"></div>
                    </div>
                </div>
                <div class="list myList">
                    <div class="head">
                        <img src="./../../assets/pic1.png">
                    </div>
                    <div class="msgText">
                        <p>加拉速度进来撒肯德基萨拉斯加拉速度进来撒肯德基萨拉斯加拉速度进来撒肯德基萨拉斯加拉速度进来撒肯德基萨拉斯</p>
                        <div class="myboo"></div>
                    </div>
                </div>
            </div>
        </div>
        <div class="user">
            <div class="userContent">
                <mt-field id="sendText" type="textarea" rows="1" v-model="myMsg"></mt-field>
                <mt-button class="send" @click="send">发送</mt-button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                myMsg: ''
            }
        },
        methods: {
            send: function(){

            }
        },
        mounted: function() {
            let h = window.screen.height;
            document.getElementById("chat").style.height = h - 137 + "px";
        }
    }
</script>

<style scoped>
.user {
  height: 40px;
  position: fixed;
  bottom: 57px;
  width: 100%;
}
.userContent {
  height: 100%;
  border-top: 1px solid #999;
   display: flex;
  justify-content: space-between;
}
#sendText {
  width: 80%;
}
.send {
  width: 20%;
}

#chat{
    overflow: hidden;
    overflow-y: auto;
}
.list{
    padding: 10px 0;
    display: flex;
    flex-wrap: nowrap;
}
.head{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
}
.head img{
    width: 100%;
    height: 100%;
}
.msgText{
    font-size: 14px;
    padding: 10px;
    max-width: 60%;
    background-color: #fff;
    border-radius: 5px;
    position: relative;
    margin-left: 20px;
}
.boo{
    position: absolute;
    width: 0;
    height: 0;
    border-top: 5px solid transparent; 
    border-right: 10px solid #fff; 
    border-bottom: 5px solid transparent; 
    border-left: 10px solid transparent; 
    top: 30%;
    left: -20px;
}
.myList{
    flex-direction: row-reverse;
}
.myList .msgText{
    margin-left: 0;
    margin-right: 20px;
    background-color: #57d6dd;
    color: #fff;
}
.myList .myboo{
    position: absolute;
    width: 0;
    height: 0;
    border-top: 5px solid transparent; 
    border-right: 10px solid transparent; 
    border-bottom: 5px solid transparent; 
    border-left: 10px solid #57d6dd; 
    top: 30%;
    right: -20px;
}
</style>


