// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Mint from 'mint-ui'
import 'mint-ui/lib/style.css'
import Axios from 'axios'
import vuex from 'vuex'
import {root,root1,root2} from './config/root.js'



Vue.config.productionTip = false
Vue.use(Mint);
Vue.prototype.$axios = Axios;
Vue.use(vuex);


// vuex
const store = new vuex.Store({
  state: {
    songInfo: {
      user: {
        nickname: '',

      },
      code: 0,
    },
    isLogin: '',
    isPageLogin: false,
    tabbarShow: true,
    medal: '',
    addFriendResult: ''
  },
  getters: {
    
  },
  mutations: {
    //定义函数，比如我要获取songInfo
    getSongInfo(state, songInfo) {
      state.songInfo = songInfo;
    },
    getIsPageLogin(state, isPageLogin) {
      state.isPageLogin = isPageLogin;
    },
    getMedal(state, medal) {
      state.medal = medal;
    },
    getAddFriend(state, addFriendResult) {
      state.addFriendResult = addFriendResult
    }
  }
});


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})


