<template>
  <div id="app">
    <div class="main">
      <transition :name="transitionName">
        <router-view/>
      </transition>
    </div>
    <tabbar v-show="!this.$store.state.isPageLogin" v-if="this.$store.state.tabbarShow"></tabbar>
    <tabbar1 v-show="!this.$store.state.isPageLogin" v-if="!this.$store.state.tabbarShow"></tabbar1>
  </div>
</template>

<script>
import tabbar from '@/components/common/tabbar'
import tabbar1 from '@/components/common/tabbar1'
import root from '@/config/root.js'
export default {
  name: 'App',
  data(){
    return {
      transitionName: '',
    }
  },
  components: {
      tabbar,
      tabbar1
  },
  watch: {
    $route(to,from) {
      if(to.meta.index > from.meta.index) {
        this.transitionName = 'slide-left';
      }else{
        this.transitionName = 'slide-right';
      }
    }
  },
  created: function(){
    // this.$store.commit("isPageLogin",this.isPageLogin);

  },
  methods: {
    
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body,div,ol,ul,li,dl,dt,dd,h1,h2,h3,h4,h5,h6,p,form,fieldset,legend,input{ margin:0; padding:0;} 
h1,h2,h3,h4,h5,h6{ font-size:100%; font-weight:normal;} 
ol,ul{ list-style:none;} 
img,fieldset{ border:0 none; display:block;} 
input{text-align: center}
html{
  margin-bottom: 55px;
  overflow: auto;
}
li{
  list-style: none;
}
.mg0{
  margin: 0
}
body{
  background-color: #e7e7e7;
 /* margin-bottom: 15px; */
}
.mint-header{
  background-color: #57d6dd!important;
}
.slide-right-enter-active,.slide-right-leave-active,
.slide-left-enter-active,.slide-left-enter-active{
  will-change: transform;
  transition: all 500ms;
  position: absolute;
  width: 100%;
}
.slide-right-enter{
  opacity: 0;
  transform: translate(-100%, 0);
}
.slide-right-leave-active {
  opacity: 0;
  transform: translate(100%, 0);
}
.slide-left-enter {
  opacity: 0;
  transform: translate(100%, 0);
}
.slide-left-leave-active {
  opacity: 0;
  transform: translate(-100%, 0);
}
.pageContent{
  padding: 0 10px;
}
a{
    text-decoration: none;
    color: #000;
}
.mint-cell,.mint-cell-wrapper{
    /* background: none!important; */
    background-image: none!important;
    
}
.loginPage input{
  text-align: left;
}

.mint-cell-wrapper{
  background-color: #fff;
}
.transPsd1 .mint-field .mint-cell-value,.transPsd2 .mint-field .mint-cell-value,.transPsd .mint-field .mint-cell-value{
    border: 1px solid #b9b9b9;
    height: 30px;
    line-height: 30px;
}
.bgc-fff{
  background-color: #fff;
}
.mint-field .mint-cell-title{
  text-align: left;
}
input[disabled]{
  background-color: #fff;
}
.interval{
  height: 10px;
  background-color: #e7e7e7;
}
.password input{
  text-align: left;
  padding: 0 10px;
  color: #b3b3b3;
}
.password .mint-field .mint-cell-value{
  border: 1px solid #b3b3b3;
  height: 40px;
  line-height: 40px;
}
.mint-switch-input:checked + .mint-switch-core {
    border-color: #57D6DD;
    background-color: #57D6DD;
}
.mint-switch-core::before{
  background-color: #c3c3c3;
}
.mt10{
  margin: 10px 0;
}
.mt30{
    margin-top: 30px;
}
.mt20{
  margin-top: 20px;
}

.mySystem .mint-cell-text{
  margin-left: 10px;
}
.bank input{
  text-align: left;
}
.bank .mint-cell-wrapper{
  border-top: 1px solid #e7e7e7;
}
.radio #radioText:focus{
  outline: none;
}
/* .findBox */
.findBox .mint-field .mint-cell-value{
    border: 1px solid #e7e7e7;
}
.findBox .mint-field-core{
  line-height: 30px;
}
.findBox .mint-radiolist-label{
  padding: 0;
  line-height: 35px;
}
.loginPage .mint-cell{
    border-radius: 5px;
    margin-top: 20px;
}
#registPage input{
  text-align: left;
}
#registPage .mint-cell{
  border-radius: 5px;
    margin-top: 20px;
}
.clearfix::after{
  content: '';
  height: 0;
  line-height: 0;
  display: block;
  visibility: hidden;
  clear: both;
}
.clearfix{
  zoom: 1;
}
</style>



